@font-face {
    font-family: 'Titillium Web';
    src: url('../fonts/Titillium/TitilliumWeb-Regular.eot');
    src: local('Titillium Web Regular'), local('TitilliumWeb-Regular'),
        url('../fonts/Titillium/TitilliumWeb-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Titillium/TitilliumWeb-Regular.woff2') format('woff2'),
        url('../fonts/Titillium/TitilliumWeb-Regular.woff') format('woff'),
        url('../fonts/Titillium/TitilliumWeb-Regular.ttf') format('truetype'),
        url('../fonts/Titillium/TitilliumWeb-Regular.svg#TitilliumWeb-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}
